<template>
  <div
    v-show="
      !hideAnswerForAlternativeQuestionTypes.includes(aqtype) &&
      !(
        qtype === 'select_platform' &&
        aqtype === 'textfield' &&
        answer === 'other'
      )
    "
    class="full-width"
  >
    <!-- No Answer -->
    <div
      v-if="!answer || answer?.length < 1"
      class="no-answer text-primary-400"
    >
      -
    </div>

    <!-- textarea1 -->
    <div
      v-else-if="qtype === 'textarea1'"
      class="raw-html"
      v-html="answer"
    ></div>

    <!-- textarea2 -->
    <div
      v-else-if="qtype === 'textarea2'"
      class="raw-html"
      v-html="answer"
    ></div>

    <!-- textfield -->
    <div v-else-if="qtype === 'textfield'">{{ answer }}</div>

    <!-- select -->
    <div v-else-if="qtype === 'select'">
      {{
        checkIfObject(answer)
          ? answer?.label || answer?.value
          : getLabelFromValue(question?.options, answer)
      }}
    </div>

    <!-- select_multiple -->
    <template v-else-if="qtype === 'select_multiple'">
      <template v-if="Array.isArray(answer)">
        <ul class="q-mt-none">
          <li v-for="ans in answer" :key="ans">
            {{
              checkIfObject(ans)
                ? ans?.label || ans?.value
                : getLabelFromValue(question?.options, ans)
            }}
          </li>
        </ul>
      </template>
      <div v-else>
        {{
          checkIfObject(answer)
            ? answer?.label || answer?.value
            : getLabelFromValue(question?.options, answer)
        }}
      </div>
    </template>

    <!-- radio -->
    <div v-else-if="qtype === 'radio'">
      {{
        checkIfObject(answer)
          ? answer?.label || answer?.value
          : getLabelFromValue(question?.options, answer)
      }}
    </div>

    <!-- checkbox -->
    <template v-else-if="qtype === 'checkbox'">
      <template v-if="Array.isArray(answer)">
        <ul class="q-mt-none">
          <li v-for="ans in answer" :key="ans">
            {{
              checkIfObject(ans)
                ? ans?.label || ans?.value
                : getLabelFromValue(question?.options, ans)
            }}
          </li>
        </ul>
      </template>
      <div v-else>
        {{
          checkIfObject(answer)
            ? answer?.label || answer?.value
            : getLabelFromValue(question?.options, answer)
        }}
      </div>
    </template>

    <!-- select_platform -->
    <template v-else-if="qtype === 'select_platform'">
      <q-badge class="badge badge-blue full-height" v-if="platforms?.[answer]">
        <div class="q-px-sm q-py-xs">
          <q-icon
            :name="`img:${platforms[answer]?.icon}`"
            size="md"
            class="q-pr-sm"
          />
          <span>{{ platforms[answer]?.title }}</span>
        </div>
      </q-badge>
      <div v-else>
        {{ typeof answer === 'string' ? capitalize(answer) : answer }}
      </div>
    </template>

    <!-- video_walkthrough -->
    <template v-else-if="qtype === 'video_walkthrough'">
      <q-video
        :ratio="16 / 9"
        class="rounded fit"
        :src="loomURLShare2Embed(answer)"
      />
    </template>

    <!-- select_brand -->
    <div v-else-if="qtype === 'select_brand'">
      {{ selectedOrgBrands[answer]?.brand_name }}
    </div>

    <!-- select_taskcategory -->
    <div v-else-if="qtype === 'select_taskcategory'">
      {{ answer }}
    </div>

    <!-- select_projectcategory -->
    <div v-else-if="qtype === 'select_projectcategory'">
      {{ answer }}
    </div>

    <!-- select_tasktype -->
    <div v-else-if="qtype === 'select_tasktype'">
      {{ answer }}
    </div>

    <!-- select_projecttype -->
    <div v-else-if="qtype === 'select_projecttype'">
      {{ answer }}
    </div>

    <!-- url -->
    <div v-else-if="qtype === 'url'">
      <a :href="`//${answer}`" target="_blank">https://{{ answer }}</a>
    </div>

    <!-- phone -->
    <div v-else-if="qtype === 'phone'">
      {{ answer ? `+${answer.split('+')[0]} ${answer.split('+')[1]}` : '' }}
    </div>

    <!-- upload_single' || qtype === 'upload_multiple -->
    <template
      v-else-if="qtype === 'upload_single' || qtype === 'upload_multiple'"
    >
      <template v-if="qtype === 'upload_multiple' && Array.isArray(answer)">
        <div v-for="ans in answer" :key="ans">
          {{ ans }}
        </div>
      </template>
      <div v-else>
        {{ answer }}
      </div>
    </template>

    <!-- website_content -->
    <template v-else-if="qtype === 'website_content'">
      <div v-if="answer?.info">{{ answer.info }}</div>
      <template v-if="answer.uploads">
        <div v-for="ans in answer.uploads" :key="ans">
          {{ ans }}
        </div>
      </template>
    </template>
  </div>
</template>

<script>
import { defineComponent } from 'vue';
import {
  checkIfObject,
  getLabelFromValue,
  loomURLShare2Embed,
} from 'src/assets/scripts/functions';
import { format } from 'quasar';
import { mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import useUser from 'stores/user';

export default defineComponent({
  name: 'DynamicAnswers',

  props: {
    qtype: {
      type: String,
      default: '',
    },
    answer: {
      default: null,
    },
    aqtype: {
      type: String,
      default: '',
    },
    question: {},
  },

  data() {
    return {
      checkIfObject,
      loomURLShare2Embed,
      getLabelFromValue,
      capitalize: format.capitalize,
      hideAnswerForAlternativeQuestionTypes: ['url'],
    };
  },

  computed: {
    ...mapState(useGrowmodo, ['platforms']),
    ...mapState(useUser, ['selectedOrgBrands']),
  },
});
</script>
